import React from 'react';
import moment from 'moment';
// import { v4 as uuid } from 'uuid';
import PerfectScrollbar from 'react-perfect-scrollbar';
import {
  Box,
  // Button,
  Card,
  CardHeader,
  // Chip,
  Divider,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  TableSortLabel,
  Tooltip
} from '@material-ui/core';
import vlist from '../../__mocks__/bilibili';

// import ArrowRightIcon from '@material-ui/icons/ArrowRight';

const numberWithComma = (number) => number.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');

const LatestContents = (props) => (
  <Card {...props}>
    <CardHeader title="Latest Orders" />
    <Divider />
    <PerfectScrollbar>
      <Box sx={{ minWidth: 800 }}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>
                컨텐츠 제목
              </TableCell>
              <TableCell>
                썸네일
              </TableCell>
              <TableCell sortDirection="desc">
                <Tooltip
                  enterDelay={300}
                  title="Sort"
                >
                  <TableSortLabel
                    active
                    direction="desc"
                  >
                    업로드 일시
                  </TableSortLabel>
                </Tooltip>
              </TableCell>
              <TableCell>
                영상길이
              </TableCell>
              <TableCell>
                영상재생 횟수
              </TableCell>
              <TableCell>
                댓글 수
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {React.Children.toArray(
              vlist.map((video) => (
                <TableRow
                  hover
                >
                  <TableCell>
                    <a href={`https://www.bilibili.com/video/${video.bvid}`} target="_blank" rel="noreferrer">
                      {video.title}
                    </a>
                  </TableCell>
                  <TableCell>
                    <a href={video.pic} target="_blank" rel="noreferrer">썸네일 보기</a>
                  </TableCell>
                  <TableCell>
                    {moment(video.created * 1000).format('YYYY-MM-DD HH:mm')}
                  </TableCell>
                  <TableCell>
                    {video.length}
                  </TableCell>
                  <TableCell>
                    {numberWithComma(video.play)}
                  </TableCell>
                  <TableCell>
                    {numberWithComma(video.comment)}
                  </TableCell>
                </TableRow>
              ))
            )}
          </TableBody>
        </Table>
      </Box>
    </PerfectScrollbar>
    {/* <Box
      sx={{
        display: 'flex',
        justifyContent: 'flex-end',
        p: 2
      }}
    >
      <Button
        color="primary"
        endIcon={<ArrowRightIcon />}
        size="small"
        variant="text"
      >
        View all
      </Button>
    </Box> */}
  </Card>
);

export default LatestContents;
